import axios from 'axios';
import React,{useEffect,useState} from 'react';
import { toast } from 'react-toastify';
import {Jumbotron,Container, Table} from 'reactstrap';
import { CustomDialog, useDialog } from 'react-st-modal';
import {Button} from 'reactstrap';
import { Redirect } from 'react-router';

class Home extends React.Component {

    // const [data,setData] = useState([]);
    // const [hide,setHide] = useState(false);
    // useEffect(() => {
    //     document.title = "Home"
    // },[])

    constructor(props){
        super(props);
        let loggedIn = true;
        this.state = {
            data:[],
            hide:false,
            loggedIn:false
        }
        
        let token = localStorage.getItem("token");
        
        if(token === null || token === undefined){
            // loggedIn = true;
            this.state.loggedIn= false;
            console.log(this.state);
            // this.setState({loggedIn:false});
        }else{
            this.state.loggedIn= true;
            // this.setState({loggedIn:true});
            // loggedIn=false;
            console.log(this.state);
        }
        // alert("logged in:"+this.state.loggedIn);

    }

    componentDidMount(){
        document.title = "Home"
    }


    btnHandle = () => {
        toast.success("Completed",{
            position:'bottom-center'
        })
    }

    btnHandle1 = () => {
        axios.get(`/getData`).then(
            (response) => {
                console.log(response.data);
                this.setState({data:response.data,hide:false});
            },
            (error) => {
                console.log(error);
                this.setState({data:[],hide:true});
            }
        )
    }

    render(){
        if(this.state.loggedIn === false){
            return <Redirect to="/" />
        }else{

        
    return(
        <div>
            <Jumbotron className="text-center">
                <p className="display-4">Learning Jumbotrons</p>
                <p>This is the page used for description</p>
                <Container>
                    <Button color="primary" onClick={this.btnHandle}>Start Using</Button>
                </Container>
            </Jumbotron>
            {/* <Button color="primary" onClick={btnHandle1}>Click</Button> */}
            <br></br>
            <br></br>
            <div className="table-responsive w-100">
                <table className="table table-bordered">
                    {
                        this.state.data.map((item,i)=>{
                            return(    
                                <tr key={i}>
                                    <td className="text-center">{item.id}</td>
                                    <td>{item.title}</td>
                                    <td>{item.description}</td>
                                    <td>{item.phone}</td>
                                    <td>{item.pincode}</td>
                                    <td>{item.date}</td>
                                </tr>
                            )
                        })
                    }
                </table>
            </div>
        </div>
    )
}
}
}

export default Home;

