import React, { Component } from 'react'
import {MenuItems} from './MenuItems'
import './Navbar.css'
import {Link, Redirect} from 'react-router-dom';
import {BrowserRouter as Router,Route} from 'react-router-dom';
import {Button } from './Button';
import * as FaIcons from 'react-icons/fa';
import * as IoIcons from 'react-icons/io';
import * as AiIcons from 'react-icons/ai';

class Navbar extends Component{

    constructor(props){
        super(props);
        let loggedIn = true;
        const token = localStorage.getItem("token");
        if(token == null ){
            loggedIn = false;
        }
        this.state = {
            loggedIn
        }
    }

    state = { clicked:false }

    handleClick = () => {
        this.setState({clicked:!this.state.clicked})
    }

    doLogout = () => {
        // alert("claaed");
        sessionStorage.removeItem("token");
        sessionStorage.clear();
        localStorage.removeItem("token");
        localStorage.clear();
        let loggedIn = true;
        const token = sessionStorage.getItem("token");
        if(token === null ){
            loggedIn = false;
        }
        this.state = {
            loggedIn
        }
        console.log("token:"+token);
        console.log(loggedIn);
        // this.setState({loggedIn:false});
        // window.location.reload(false);
    }

    render(){
        if(this.state.loggedIn===false){
            return(
                <Redirect to="/" />
            )
        }
        return(
            <nav className="NavbarItems">
                <Link to="/" style={{textDecoration:'none'}}><h1 className="navbar-logo">React<i className="fab fa-react"></i></h1></Link>
                <div className="menu-icon" onClick={this.handleClick}>
                        <i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i>
                </div>
                <ul className={this.state.clicked ? 'nav-menu active' : 'nav-menu'} onClick={this.handleClick}>
                    {
                        MenuItems.map((item,index) => {
                            return(
                                <li key={index}>
                                    <Link className={item.cName} to={item.url}>
                                        <span style={{ margin:'5px'}}>{item.icon}</span>
                                        {item.title}
                                    </Link>
                                </li>
                            )
                        })
                    }                    
                </ul>
                <div className="text-right">
                <a href="/" className="btn btn-primary float-right" style={{color:"white",marginLeft:'9rem'}}  onClick={this.doLogout} value="Logout">Logout</a>
                </div>
            </nav>
        )
    }
}

export default Navbar;
