import { toast } from 'react-toastify';
import axios from 'axios';
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import img from '../images/altumcode-dMUt0X3f59Q-unsplash.jpg';
const initialState = {
        id:0,
        firstname:'',
        lastname:'',
        email:'',
        password:'',
        token:'',
        fnameError:'',
        lnameError:'',
        emailError:'',
        passError:''
}

export default class Registration extends Component {

    state = initialState;
    
    constructor(props){
        super(props);    
        this.submitForm = this.submitForm.bind(this);
        this.onChange = this.onChange.bind(this);
        console.log(this.state);
    }

    onChange(e){
        this.setState({
            [e.target.name]:e.target.value
        })
        console.log(this.state);
    }

    validate = () => {
        let fnameError='';
        let lnameError='';
        let emailError='';
        let passError='';

        const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);

        if(!this.state.firstname){
            fnameError = "Firstname cannot be blank";
        }

        if(!this.state.lastname){
            lnameError = "Lastname cannot be blank";
        }

        if(!validEmailRegex.test(this.state.email)){
            emailError = "Invalid email";
        }

        if(!this.state.password || this.state.password.length < 8){
            passError = "Invalid password";
        }

        if(fnameError || lnameError || emailError || passError){
            this.setState({fnameError,lnameError,emailError,passError});
            return false;
        }
        return true;
    };

    submitForm(e){
        e.preventDefault();
        const isValid = this.validate();
        if(isValid){
        const crypto = require('crypto');
        // const randomBytes = Util.promisify(crypto.randomBytes)
        var token = crypto.randomBytes(24).toString('hex');
        this.state.token = token;
        var decodedStringBtoA = this.state.password;
       // Encode the String
       var encodedStringBtoA = btoa(decodedStringBtoA);
        this.state.password = encodedStringBtoA;
    //    alert(encodedStringBtoA);
       // Define the string
       var encodedStringAtoB = encodedStringBtoA;
       // Decode the String
       var decodedStringAtoB = atob(encodedStringAtoB);
    //    alert(decodedStringAtoB);
            let data = {
                id:this.state.id,
                firstname : this.state.firstname,
                lastname : this.state.lastname,
                email:this.state.email,
                password:this.state.password,
                token:this.state.token
            }
        axios.post("/regi",data)
        .then((res)=>{
            console.log(res.data);
            toast.success("user has been added",{});
        })
        .catch((err)=>{
            console.log(err);
            toast.error("user hasn't been added",{});
        })
        console.log(this.state);
        this.setState(initialState);
    }
    }

    getData = (e) => {
        e.preventDefault();
        axios.get("/getUsers")
        .then((res)=>{
            console.log(res.data);
        })
        .catch((err)=>{
            console.log(err);
        })
    }

    render() {
        return (
            <div className="text-center imgCls">
                <div style={{paddingTop:'10px'}}>
                    <h1>Registration page</h1>
                </div>
                

                <form onSubmit={this.submitForm}>
                    {/* <div className="col-sm-4 offset-sm-4">
                        <label>Id</label>
                        <input className="form-control" type="text" name="id" onChange={this.onChange} placeholder="Enter id"></input>
                    </div> */}
                    <div className="col-sm-4 offset-sm-4">
                        <label>First Name</label>
                        <input  className="form-control" type="text" name="firstname" value={this.state.firstname} onChange={this.onChange} placeholder="Enter firstname"></input>
                    </div>
                    <div style={{marginTop:'10px',fontSize:12,color:'red'}}>
                    {this.state.fnameError}
                    </div>
                    <div className="col-sm-4 offset-sm-4">
                        <label>Last Name</label>
                        <input className="form-control" type="text" name="lastname" value={this.state.lastname} onChange={this.onChange} placeholder="Enter lastname"></input>
                    </div>
                    <div style={{marginTop:'10px',fontSize:12,color:'red'}}>
                    {this.state.lnameError}
                    </div>
                    <div className="col-sm-4 offset-sm-4">
                        <label>Email</label>
                        <input className="form-control" type="text" name="email" value={this.state.email} placeholder="Enter email" onChange={this.onChange}></input>
                    </div>
                    <div style={{marginTop:'10px',fontSize:12,color:'red'}}>
                    {this.state.emailError}
                    </div>
                    <div className="col-sm-4 offset-sm-4">
                        <label>Password</label>
                        <input className="form-control" type="password" name="password" value={this.state.password} placeholder="Enter password" onChange={this.onChange}></input>
                    </div>
                    <div style={{marginTop:'10px',fontSize:12,color:'red'}}>
                    {this.state.passError}
                    </div>
                    <div className="col-sm-4 offset-sm-4" style={{marginTop:'15px'}}>
                        <input type="submit" className="btn btn-primary" style={{marginRight:'10px'}} value="Register"></input>
                        <Link style={{textDecoration:'none'}} to="/login">Login</Link>
                        {/* <button className="btn btn-secondary" style={{marginLeft:'10px'}} onClick={this.getData}>Getdata</button> */}
                    </div>
                </form>
            </div>
        )
    }
}
