    onChange(e) {
        if (e.target.type === "password") {
            let pass = e.target.value;
            var decodedStringBtoA = pass;
            var password = btoa(decodedStringBtoA);
            this.state.password = password;
            this.state.r_password = password;
        }
        this.setState({
            [e.target.name]: e.target.value
        });
        console.log(this.state);


    }

    //registration page code

    validate_reg = () => {
        let fnameError = '';
        let lnameError = '';
        let r_emailError = '';
        let r_passError = '';

        const validEmailRegex = RegExp(/^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i);

        if (!this.state.r_firstname) {
            fnameError = "Firstname cannot be blank";
        }

        if (!this.state.r_lastname) {
            lnameError = "Lastname cannot be blank";
        }

        if (!validEmailRegex.test(this.state.r_email)) {
            r_emailError = "Invalid email";
        }

        if (!this.state.r_password || this.state.r_password.length < 8) {
            r_passError = "Invalid password";
        }

        if (fnameError || lnameError || r_emailError || r_passError) {
            this.setState({ fnameError, lnameError, r_emailError, r_passError });
            return false;
        }
        return true;
    };

    submitForm_reg(e) {
        e.preventDefault();
        const isValid = this.validate_reg();
        if (isValid) {
            const crypto = require('crypto');
            // const randomBytes = Util.promisify(crypto.randomBytes)
            var token = crypto.randomBytes(24).toString('hex');
            this.state.token = token;
            var decodedStringBtoA = this.state.r_password;
            // Encode the String
            var encodedStringBtoA = btoa(decodedStringBtoA);
            this.state.r_password = encodedStringBtoA;
            //    alert(encodedStringBtoA);
            // Define the string
            var encodedStringAtoB = encodedStringBtoA;
            // Decode the String
            var decodedStringAtoB = atob(encodedStringAtoB);
            //    alert(decodedStringAtoB);
            let data = {
                // id:this.state.id,
                firstname: this.state.r_firstname,
                lastname: this.state.r_lastname,
                email: this.state.r_email,
                password: this.state.r_password,
                token: this.state.token
            }
            axios.post("/regi", data)
                .then((res) => {
                    console.log(res.data);
                    toast.success("user has been added", {});
                })
                .catch((err) => {
                    console.log(err);
                    toast.error("user hasn't been added", {});
                })
            console.log(this.state);
            this.setState(initialState);
        }

        // getData = (e) => {
        //     e.preventDefault();
        //     axios.get("/getUsers")
        //     .then((res)=>{
        //         console.log(res.data);
        //     })
        //     .catch((err)=>{
        //         console.log(err);
        //     })
        // }

    }


