import React, {useState, useEffect} from "react";
import Course from './Course';
import GlitchClip from 'react-glitch-effect/core/GlitchClip';
import  {BeatLoader,CircleLoader,PulseLoader} from 'react-spinners';
import base_url from '../api/bootapi'
import {
    Audio,
    BallTriangle,
    Bars,
    Circles,
    Grid,
    Hearts,
    Oval,
    Puff,
    Rings,
    SpinningCircles,
    TailSpin,
    ThreeDots,
  } from '@agney/react-loading';
import { BarsSpinner,ClapSpinner,StageSpinner ,MagicSpinner} from "react-spinners-kit";
import axios from 'axios';
import {toast} from 'react-toastify';
class Allcourse extends React.Component {
    

    // const [loading,setLoading] = useState(false);
    constructor(props){
        super(props);
        this.state = {
            courses:[],
            loading:false
        }
    }

    //functuion to call server
    getAllCoursesFromServer = () => {
        axios.get(`/courses`).then(
            (res) => {
                console.log(res.data);
                console.log("asdasd");
                toast.success("courses have been loaded",{
                    position: 'bottom-center'
                });
                // setCourses(response.data);
                this.setState({courses:res.data,loading:true});
            },
            (error) => {
                console.log(error);
                toast.error("error");
                this.setState({loading:true});
            }
        );
    };

    //loading course function
    // useEffect(() => {
    //     document.title = "All Courses";
    //     getAllCoursesFromServer();
    // },[]);

    // const [courses,setCourses] = useState([]);

    componentDidMount(){
        document.title = "All Courses";
        this.getAllCoursesFromServer();
    }

    updateCourses = (id) => {
        this.setState(this.state.courses.filter((c) => c.id !== id));
    }

    render(){

    
    return(
        <div>
            {this.state.loading ? <div></div> :<div className="centered"> 
                <BeatLoader />    
                </div>
                }
            
            <h1 style={{color:"white"}}>All Courses</h1>
            <p className="spanCls">List of courses are as Follows</p>
            <div style={{padding:10,fontFamily:'verdana'}}>
                {
                    this.state.courses.length > 0 ? this.state.courses.map((item)=>
                    
                    <Course key={item.id} course={item} 
                    update={this.updateCourses} />
                    
                    ) : "No courses"
                }
            </div>
        </div>
    );
}
}

export default Allcourse;