import axios from 'axios';
import React, { Fragment, useEffect, useState } from 'react';
import { Form, FormGroup, Button, Container, Input } from 'reactstrap';
import { toast } from 'react-toastify';
import base_url from '../api/bootapi';
import { FaSleigh } from 'react-icons/fa';
class AddCourse extends React.Component {

    // useEffect(() => {
    //     console.log("successs");
    //        document.title = "Add New Course";
    //        loadFunction();
    // },[])

    // const [course,setCourse] = useState({});

    // state = {};

    constructor(props) {
        super(props);
        this.onChange = this.onChange.bind(this);
        // this.submitForm = this.submitForm.bind(this);
        const token = localStorage.getItem('token');
        let tokenFound = false;
        let loggedIn = true;
        this.state = {
                id:'',
                title:'',
                description:'',
                phone:'',
                pincode:'',
                date:'',
            loggedIn:false,
            tokenFound:false
        }
        if (token === null) {
            loggedIn = false;
            this.setState({loggedIn:false});
        }else{
            this.setState({loggedIn:true});
        }
        console.log(this.state);
    }

    componentDidMount(){
        console.log("successs");
           document.title = "Add New Course";
           this.loadFunction();
    }

    //form handler func
    handleForm = (e) => {
        console.log(this.state.course);
        this.postDataToServer(this.state);
        e.preventDefault();
    }


    //post data to server
    postDataToServer = (data) => {
        axios.post(`/courses`, data).then(
            (response) => {
                console.log("success");
                toast.success("course has been added", {});
            },
            (error) => {
                console.log("error");
                toast.error("something went wrong", {});
            }
        );
    }

    loadFunction = () => {
        axios.get(`/courses`).then(
            (response) => {
                console.log("success");
                // this.setState({course:response.data});
            },
            (error) => {
                console.log("error");
            }
        )
    }

    onChange(e){
        this.setState({
            [e.target.name]: e.target.value
        });
        console.log(this.state);

    }

    render(){

    return(
        <Fragment>
            <h1 className="text-center my-2" style={{color:'#fff'}}>Fill Course details</h1>
            <Form onSubmit={this.handleForm}>
                
                <FormGroup>
                    <label for="id">ID</label>
                    <input
                        className="form-control"
                        type="text"
                        placeholder="Enter id"
                        name="id"
                        id="id"
                        onChange={this.onChange}
                        value={this.state.id}
                    />
                </FormGroup>

                <FormGroup>
                    <label for="title">Course Title</label>
                    <input
                        className="form-control"
                        type="text"
                        placeholder="Enter title"
                        name="title"
                        id="title"
                        onChange={this.onChange}
                        value={this.state.title}
                    />
                </FormGroup>

                <FormGroup>
                    <label for="description">Course Description</label>
                    <input
                        className="form-control"    
                        type="textarea"
                        placeholder="Enter description"
                        name="description"
                        id="description"
                        style={{height:'150px'}}
                        onChange={this.onChange}
                        value={this.state.description}
                    />
                </FormGroup>
                <div className="row">
                    <div className="col-md-6">
                    <FormGroup>
                                    <label for="phone">Contact No.</label>
                                    <input
                                    className="form-control"
                                        type="text"
                                        placeholder="Enter Phone"
                                        name="phone"
                                        id="phone"
                                        onChange={this.onChange}
                                        value={this.state.phone}
                                    />
                                </FormGroup>

                    </div>
                    <div className="col-md-6">
                    <FormGroup>
                                    <label for="pincode">Pincode</label>
                                    <input
                                    className="form-control"
                                        type="text"
                                        placeholder="Enter Pincode"
                                        name="pincode"
                                        id="pincode"
                                        onChange={this.onChange}
                                        value={this.state.pincode}
                                    />
                                </FormGroup>

                    </div>
                    <div className="col-md-6">
                        <FormGroup>
                            <label for="date">Date Added</label>
                            <input
                                type="date"
                                className="form-control"
                                placeholder="Date"
                                name="date"
                                id="date"
                                onChange={this.onChange}
                                value={this.state.date}
                            />
                        </FormGroup>
                    </div>
                </div>

                <Container className="text-center">
                    <Button type="submit" color="primary">Add Course</Button>
                    {/*
                        <Button className="ml-2" onClick={() => {
                        console.log(course.id);
                        }}>Read</Button>
                    */}
                    <Button type="reset" onClick={() => {
                        console.log(this.state);
                        // this.setState({id: "", title:"", description:"",pincode:"",phone:"",date:""});
                    }} color="secondary ml-2">Clear</Button>
                    <br></br>
                    <br></br>
                </Container>

            </Form>
        </Fragment >
    )
}
}

export default AddCourse;